import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { Location } from '@angular/common';
import { Select, Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { GlobalInfoState } from '../../@store/global-info/global-info.state';
import { Observable, Subscription } from 'rxjs';
import { IElement } from '../../@store/global-info/global-info.model';
import {
  GetAcademicOrganizationInfo,
  GetOtherOrganizationTypeInfo,
  GetSportOrganizationInfo,
} from '../../@store/global-info/global-info.actions';
import { TranslateService } from '@ngx-translate/core';
import { OrganizationTypes } from '../organization-types';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OrganizationBasePage } from '../organization-base.page';
import { StorageService } from '../../@core/storage.service';
import { Utils } from '../../@global/utils';
import { UpdateCurrentPageData } from '../../@store/pages/pages.actions';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../../@core/inform.service';

const PAGE_POSITION = 1;

@Component({
  selector: 'app-subtype-organization',
  templateUrl: './subtype-organization.page.html',
  styleUrls: ['./subtype-organization.page.scss'],
})
export class SubtypeOrganizationPage extends OrganizationBasePage implements OnInit, OnDestroy {
  public organizationType = OrganizationTypes.Sport;
  public subtypesList: any;
  public title: string;
  public text: string;
  public prefix: string;
  public textLines = [];
  public label: string;
  public formGroup: FormGroup;
  public customActionSheetOptions: any;

  @Select(GlobalInfoState.getAcademicSubtypes) academic$: Observable<IElement[]>;
  @Select(GlobalInfoState.getSportSubtypes) sports$: Observable<IElement[]>;
  @Select(GlobalInfoState.getOtherSubtypes) other$: Observable<IElement[]>;
  private subtypesSubscription$: Subscription;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    private translateService: TranslateService,
    private formBuilder: FormBuilder
  ) {
    super(location, router, store, utilsService, camera, informService, storage);
    if (this.router.getCurrentNavigation().extras.state) {
      this.organizationType = this.router.getCurrentNavigation().extras.state.organizationType;
    }
  }

  ngOnInit() {
    this.subtypesSubscription$ = this.sports$.subscribe((data) => {
      this.subtypesList = data;
    });
    this.subtypesSubscription$ = this.academic$.subscribe((data) => {
      this.subtypesList = data;
    });
    this.subtypesSubscription$ = this.other$.subscribe((data) => {
      this.subtypesList = data;
    });
    this.initForm();
  }

  public ionViewWillEnter() {
    this.loadSubtypes();
    this.title = this.translateService.instant(`subtype-organization.option${this.organizationType}`);
    this.text = this.translateService.instant(`subtype-organization.text${this.organizationType}`);
    this.label = this.translateService.instant(`subtype-organization.label${this.organizationType}`);
    this.textLines = this.text.split('.');
    this.customActionSheetOptions = {
      header: this.label,
      translucent: true,
    };

    this.prefix = Utils.getSubtypeTranslationPrefix(this.organizationType);
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      subtype: ['', [Validators.required]],
    });
  }

  private loadSubtypes() {
    switch (this.organizationType) {
      case OrganizationTypes.Sport:
        this.store.dispatch(new GetSportOrganizationInfo());
        break;
      case OrganizationTypes.Academic:
        this.store.dispatch(new GetAcademicOrganizationInfo());
        break;
      case OrganizationTypes.Other:
        this.store.dispatch(new GetOtherOrganizationTypeInfo());
        break;
      default:
        console.error('No type selected');
        break;
    }
  }

  public async goNextPage() {
    if (this.utilsService.isValidForm(this.formGroup)) {
      this.store.dispatch(new UpdateCurrentPageData({ organization_subtype: this.formGroup.get('subtype').value }));

      const navigationExtras: NavigationExtras = {
        state: {
          organizationType: this.organizationType,
        },
      };

      this.goNextFlowPage(PAGE_POSITION, navigationExtras);
    }
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }

  private disconnectObservers(): void {
    this.subtypesSubscription$.unsubscribe();
  }
}
